}

.chatroom{
display: flex;
justify-content: space-evenly;
flex: 1 1 100%;
padding: 3rem;
}

.users{
    width:15%;
 img {
     border-radius: 50%;
     height: 5rem;
 }
}

.content{
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 70%;
    min-width: 50%;
    background-color: whitesmoke;
    padding: 2rem;
    border-radius: 8%;
    line-height: 1.5rem;
}

.addMessage{
    bottom: 1rem;
}

.messages{
    display: flex;
    flex-direction: column;

}

.message{
    display: flex;
    flex-direction: column;
}

